<template>
  <md-toolbar class="md-primary">
    <h2 class="md-title" style="flex: 1">Vue Material</h2>

    <router-link v-for="(route, index) in routes" :key="index" :to="{ path: route.path }">
      <md-button :class="[randomClass]">
        <span class="label">{{route.label}} &nbsp;</span>
      </md-button>
    </router-link>
  </md-toolbar>
</template>

<script>
  import { routes } from '../router';

  export default {
    name: 'links',
    data() {
      return {
        routes,
        buttonClasses: [
          '',
          'md-primary',
          'md-accent',
          'md-warn',
          'md-dense',
        ],
      };
    },
    methods: {
      randomClass() {
//        return 'md-dense';
        return this.buttonClasses[
          Math.floor(Math.random() * this.boardClasses.length)
        ];
      }
    },
  };
</script>

<style>
  .label {
    color: #fff;
  }

  .router-link-exact-active {
    background-color: #00bcd4;
  }
</style>



